.select {
    margin-bottom: 15px;
}
.select label {
    margin: 0 0 3px 0;
    padding: 0;
    font-weight: bold;
    display: block;
}
.select select {
    display: block;
    box-sizing: border-box;
    border: 1px solid #bebebe;
    border-radius: 3px;
    margin: 0 0 10px;
    height: 30px;
    width: 100%;
    outline: none;
    transition: all .3s ease-in-out;
}
.select select:hover {
    border: 1px solid black;
}

@media screen and (max-width: 800px) {
    .select {
        margin-bottom: 7px;
    }
    .select select {
        border-radius: 3px;
        margin: 0 0 5px;
        height: 15px;
    }
}